// Get friendly dates for a timestamp
// Can also accept JSON serialized stuff
// e.g. /Date(1423878591000)/
// ---------------------------------
Object.defineProperty(String.prototype, 'toDate', {
    value: function() {
        var timestamp = this.toString();

        if (timestamp.indexOf('/Date') > -1) {
            timestamp = timestamp.replace(/\/Date\((-?\d+)\)\//, '$1');
        }

        var date = new Date(parseInt(timestamp, 10));
        var iso = date.toISOString();
        var human = false;

        if (typeof moment === 'function' && moment(iso).isValid()) {
            human = moment(iso).fromNow();
        }

        return {
            iso: iso,
            date: date.toLocaleDateString(),
            time: date.toLocaleTimeString(),
            human: human,
        };
    },
});

// Replace all
// ---------------------------------
Object.defineProperty(String.prototype, 'replaceAll', {
    value: function(find, replace) {
        if (typeof replace === 'undefined') {
            replace = '';
        }
        return this.toString().replace(new RegExp(find.replace(/([.*+?^=!:${}()|[\]/\\])/g, '\\$1'), 'g'), replace);
    },
});

// Remove all whitespace
// ---------------------------------
Object.defineProperty(String.prototype, 'trimAll', {
    value: function() {
        return this.toString().replace(/\s/g, '');
    },
});

// Decode HTML entities properly
// ---------------------------------
Object.defineProperty(String.prototype, 'decodeHtml', {
    value: function() {
        var string = this.toString();
        var element = document.createElement('div');

        // Strip script/html tags
        string = string.replace(/<script[^>]*>([\S\s]*?)<\/script>/gim, '');
        string = string.replace(/<\/?\w(?:[^"'>]|"[^"]*"|'[^']*')*>/gim, '');

        // Set/get string
        element.innerHTML = string;
        string = element.textContent;
        element.textContent = '';

        // Prevent leaks
        element = null;

        return string;
    },
});

// Decode unicode entities properly
// https://stackoverflow.com/questions/18233091/json-loads-with-escape-characters
// https://stackoverflow.com/questions/889941/which-encoding-uses-the-x-backslash-x-prefix/889950
// ---------------------------------
Object.defineProperty(String.prototype, 'decodeUnicode', {
    value: function() {
        try {
            return JSON.parse(`"${this.replace(/\\\x([0-9|A-F|a-f]{2})/g, '\\u00$1')}"`);
        } catch (e) {
            return this;
        }
    },
});

// Strip html
// ---------------------------------
Object.defineProperty(String.prototype, 'stripHTML', {
    value: function() {
        var tmp = document.createElement('div');
        tmp.innerHTML = this.toString();
        return tmp.textContent || tmp.innerText || '';
    },
});

// Check filename is image
// ---------------------------------
Object.defineProperty(String.prototype, 'isImage', {
    value: function() {
        var filename = this.toString();

        if (filename.includes('://')) {
            filename = engine.utils.parseURL(filename).filename;
        }

        return /\.(gif|jpe?g|png|svg)$/i.test(filename);
    },
});

// Replicate C# string.format in JavaScript
// http://stackoverflow.com/a/18405800
// ---------------------------------
Object.defineProperty(String.prototype, 'format', {
    value: function() {
        var args = arguments;

        return this.toString().replace(/{(\d+)}/g, function(match, index) {
            return args[index] !== null && typeof args[index] !== 'undefined' ? args[index] : '';
        });
    },
});

// Parse integer from string
// ---------------------------------
Object.defineProperty(String.prototype, 'parseInt', {
    value: function() {
        return window.parseInt(this.toString().replace(/\D/g, ''), 10);
    },
});

// Parse integer from string
// ---------------------------------
Object.defineProperty(String.prototype, 'decodeSpaces', {
    value: function() {
        return this.toString().replaceAll('&nbsp;', ' ');
    },
});

// Check if string is HTML
// ---------------------------------
Object.defineProperty(String.prototype, 'isHTML', {
    value: function() {
        var doc = new DOMParser().parseFromString(this.toString(), 'text/html');
        return [].slice.call(doc.body.childNodes).some(function(node) {
            return node.nodeType === 1;
        });
    },
});

// Check if string is a valid absolute URL
// ---------------------------------
Object.defineProperty(String.prototype, 'isURL', {
    value: function() {
        try {
            var url = new URL(this.toString().trim());
            return url.hostname.length > 0;
        } catch (e) {
            return false;
        }
    },
});

// Selector helpers
// ---------------------------------
Object.defineProperty(String.prototype, 'toClassSelector', {
    value: function() {
        return `.${this.toString()}`;
    },
});
Object.defineProperty(String.prototype, 'toIdSelector', {
    value: function() {
        return `#${this.toString()}`;
    },
});
Object.defineProperty(String.prototype, 'toDataSelector', {
    value: function() {
        return `[data-${this.toString()}]`;
    },
});
Object.defineProperty(String.prototype, 'toNameSelector', {
    value: function() {
        return `[name="${this.toString()}"]`;
    },
});
Object.defineProperty(String.prototype, 'toDataKey', {
    value: function() {
        return this.toString()
            .replace('[data-', '')
            .replace(']', '');
    },
});

// Convert string to unicode
// ---------------------------------
Object.defineProperty(String.prototype, 'toUnicode', {
    value: function() {
        var unicode = '';
        var input = this.toString();

        for (var i = 0; i < input.length; i++) {
            var code = input
                .charCodeAt(i)
                .toString(16)
                .toUpperCase();

            while (code.length < 4) {
                code = '0' + code;
            }

            code = '\\u' + code;
            unicode += code;
        }
        return unicode;
    },
});

// Return new array with only distinct values
// ---------------------------------
Object.defineProperty(Array.prototype, 'distinct', {
    value: function(key) {
        var seen = [];

        var filtered = this.filter(function(item) {
            // Treat object as string if no key supplied
            if (typeof item === 'object') {
                if (typeof key === 'string' && key in item) {
                    item = item[key];
                } else {
                    item = JSON.stringify(item);
                }
            }

            // Bail if already seen
            if (seen.includes(item)) {
                return false;
            }

            seen.push(item);

            return true;
        });

        return filtered;
    },
});

// Find closest value in array of numbers
// ---------------------------------
Object.defineProperty(Array.prototype, 'closest', {
    value: function(target) {
        if (this.includes(target) || !this.length) {
            return target;
        }

        return this.reduce(function(previous, current) {
            return Math.abs(current - target) < Math.abs(previous - target) ? current : previous;
        });
    },
});

// Remove element(s) from an array
// ---------------------------------
Object.defineProperty(Array.prototype, 'remove', {
    value: function(items, all) {
        // Remove empty string by default
        if (engine.utils.is.undefined(items)) {
            items = [''];
        }

        // Convert to array
        if (!engine.utils.is.array(items)) {
            items = [items];
        }

        // Remove all by default
        if (!engine.utils.is.boolean(all)) {
            all = true;
        }

        var removed = [];

        for (var x = 0; x < items.length; x++) {
            var item = items[x];

            if (all) {
                for (var i = this.length; i--; ) {
                    if (this[i] === item) {
                        this.splice(i, 1);
                        removed.push(item);
                    }
                }
            } else if (this.includes(item)) {
                this.splice(this.indexOf(item), 1);
                removed.push(item);
            }
        }

        return removed;
    },
});

// Clone array
// ---------------------------------
Object.defineProperty(Array.prototype, 'clone', {
    value: function() {
        return this.slice(0);
    },
});

// Group array of objects
// ---------------------------------
Object.defineProperty(Array.prototype, 'groupBy', {
    value: function(key, sort) {
        var groups = [];

        // Bail if no key supplied
        if (typeof key !== 'string' || !key.length) {
            return [];
        }

        // Group by key
        this.forEach(function(item) {
            if (typeof item !== 'object') {
                return;
            }

            var title = item[key] || engine.locale.text.other;
            var group = groups.filter(function(group) {
                return group.title === title;
            });

            if (group.length) {
                group[0].items.push(item);
            } else {
                groups.push({
                    title: title,
                    items: [item],
                });
            }
        });

        // Put "Other" at the end
        if (sort) {
            groups = groups.sort(function(a, b) {
                // Ensure "Other" is last
                if (a.title === engine.locale.text.other || b.title === engine.locale.text.other) {
                    return -1;
                }

                return a.title.localeCompare(b.title);
            });
        }

        return groups;
    },
});

// Get the index of object with matching property/value
Object.defineProperty(Array.prototype, 'getIndex', {
    value: function(property, value) {
        var result = -1;

        this.some(function(item, index) {
            if (item[property] === value) {
                result = index;
                return true;
            }
        });

        return result;
    },
});

// Get filename from a URL
// ---------------------------------
Object.defineProperty(URL.prototype, 'filename', {
    get: function() {
        return this.pathname.split('/').pop();
    },
});

// Get relative href from a URL
// Not ideal to overwrite .toString() but meh
// ---------------------------------
Object.defineProperty(URL.prototype, 'toString', {
    value: function(relative = false) {
        if (relative) {
            return `${this.pathname}${this.search}${this.hash}`;
        }

        return this.href;
    },
});

// Get the domain from a hostname
// TLD list: http://data.iana.org/TLD/tlds-alpha-by-domain.txt
// ---------------------------------
Object.defineProperty(URL.prototype, 'domain', {
    get: function() {
        let domain = this.hostname;
        const parts = domain.split('.');
        const { length } = parts;

        // Extract the root domain
        if (length > 2) {
            domain = `${parts[length - 2]}.${parts[length - 1]}`;

            // Check to see if it's using a Country Code Top Level Domain (ccTLD) (i.e. ".me.uk")
            if (parts[length - 2].length === 2 && parts[length - 1].length === 2) {
                domain = `${parts[length - 3]}.${domain}`;
            }
        }

        return domain;
    },
});

// ---------------------------------
// Deprecated
// ---------------------------------

// Contains for array
// Deprecated in favour of polyfilled .includes
// ---------------------------------
Object.defineProperty(Array.prototype, 'contains', {
    value: function(value) {
        return Array.prototype.includes.call(this, value);
    },
});

// Contains for string
// Deprecated in favour of polyfilled .includes
// ---------------------------------
Object.defineProperty(String.prototype, 'contains', {
    value: function(value) {
        return String.prototype.includes.call(this, value);
    },
});

// ==========================================================================
// Local & Session Storage
// ==========================================================================

var engine = engine || {};

engine.storage = (function(api) {
    var config;
    var storage;

    api.init = function(settings) {
        config = {
            ttl: 86400, // Default of 24 hrs (in seconds)
            ttlKey: '__ttl',
        };
        $.extend(true, config, settings);

        // Determine which method to use
        storage = {
            local: supportsLocalStorage() ? window.localStorage : new Polyfill('local'),
            session: supportsSessionStorage() ? window.sessionStorage : new Polyfill('session'),
        };

        setup();
    };

    function setup() {
        subscribers();
        api.clean();
    }

    function subscribers() {
        // Check for background updates when tab/window is inactive
        $(window).bind('storage', function(event) {
            if (engine.browser.isIe && document.hasFocus()) {
                return;
            }

            // engine.debug.log("Background storage update");

            // Pub sub the event for same domain
            engine.publish(engine.siren.storage.synced, [{
                key: event.originalEvent.key,
                data: event.originalEvent.newValue,
            }]);
        });
    }

    // Cookies polyfill
    function Polyfill(type) {
        function createCookie(name, value, ttl) {
            var date = new Date(ttl).toGMTString();
            var expires = '; expires=' + date;

            document.cookie = name + '=' + value + expires + '; path=/';
        }

        function readCookie(name) {
            var nameEQ = name + '=';
            var ca = document.cookie.split(';');
            var i;
            var c;

            for (i = 0; i < ca.length; i++) {
                c = ca[i];
                while (c.charAt(0) === ' ') {
                    c = c.substring(1, c.length);
                }

                if (c.indexOf(nameEQ) === 0) {
                    return c.substring(nameEQ.length, c.length);
                }
            }
            return null;
        }

        function setData(data, ttl) {
            data = JSON.stringify(data);
            if (type === 'session') {
                window.name = data;
            } else {
                createCookie('localStorage', data, ttl);
            }
        }

        function clearData() {
            if (type === 'session') {
                window.name = '';
            } else {
                createCookie('localStorage', '', -86400);
            }
        }

        function getData() {
            var data = type === 'session' ? window.name : readCookie('localStorage');

            // Try and parse JSON, window.name could be the frame id
            if (data) {
                try {
                    data = JSON.parse(data);
                } catch (e) {
                    data = {};
                }
            }

            return data ? data : {};
        }

        // initialise if there's already data
        var data = getData();

        return {
            length: 0,
            clear: function() {
                data = {};
                this.length = 0;
                clearData();
            },
            getItem: function(key) {
                return data[key] === undefined ? null : data[key];
            },
            key: function(i) {
                // not perfect, but works
                var ctr = 0;
                for (var k in data) {
                    if (ctr === i) {
                        return k;
                    } else {
                        ctr++;
                    }
                }
                return null;
            },
            removeItem: function(key) {
                delete data[key];
                this.length--;
                setData(data, '');
            },
            setItem: function(key, value, ttl) {
                data[key] = value + ''; // forces the value to a string
                this.length++;
                setData(data, ttl || '');
            },
        };
    }

    // Check support (Modernizr dependency)
    function supportsLocalStorage() {
        return typeof Modernizr !== 'undefined' && Modernizr.localstorage;
    }

    function supportsSessionStorage() {
        return typeof Modernizr !== 'undefined' && Modernizr.sessionstorage;
    }

    // Is it past it's TTL ?
    function valid(key) {
        return parseInt(storage.local.getItem(key + config.ttlKey), 10) > Date.now();
    }

    // Set data in storage
    api.set = function(type, data, persist, raw) {
        // If there's no data or it's wrong, fail
        if (!engine.utils.is.object(data)) {
            return false;
        }

        // Stringify json if needed
        var storageData = raw || typeof data.data === 'string' ? data.data : JSON.stringify(data.data);

        // Just incase we've maxed quota
        try {
            if (type === 'session') {
                storage.session.setItem(data.key, storageData);
            } else if (type === 'local') {
                // With local storage, we set an additional ttl key to invalidate the data
                var expires = Date.now() + (typeof data.ttl !== 'undefined' ? data.ttl : config.ttl) * 1000;

                if (supportsLocalStorage()) {
                    storage.local.setItem(data.key, storageData);

                    // Set a ttl if we're not persisting
                    if (!persist) {
                        storage.local.setItem(data.key + config.ttlKey, expires);
                    }
                } else {
                    if (persist) {
                        // Default to 1 year
                        storage.local.setItem(data.key, storageData, Date.now() + 365 * 86400);
                    } else {
                        storage.local.setItem(data.key, storageData, expires);
                    }
                }
            }
        } catch (e) {
            // Do nothing
        }

        // Log
        engine.debug.info('Set `' + data.key + '` in ' + type + ' storage');

        // Check data was written to storage
        return storageData === engine.storage.get(type, data.key, true);
    };

    // Fetch data from storage
    api.get = function(type, key, raw) {
        if (api.exists(type, key)) {
            var data = storage[type].getItem(key);

            if (!raw) {
                try {
                    data = JSON.parse(data);
                } catch (e) {
                    // Do nothing
                }
            }
            return data;
        }

        return false;
    };

    // Get all items
    api.getAll = function(type, raw) {
        var archive = {};
        var keys = Object.keys(type === 'local' ? storage.local : storage.session);

        if (!type || !keys.length) {
            return false;
        }

        $.each(keys, function(i) {
            archive[keys[i]] = api.get(type, keys[i], raw);
        });

        return archive;
    };

    // Set all items
    api.setAll = function(type, data, raw) {
        if (!type) {
            return false;
        }

        if (!data) {
            api.removeAll(type);
        } else {
            $.each(data, function(key, storageData) {
                api.set(
                    type,
                    {
                        key: key,
                        data: storageData,
                    },
                    true,
                    raw,
                );
            });
        }
    };

    // Remove an item
    api.remove = function(type, key) {
        if (type === 'session') {
            storage.session.removeItem(key);
        } else if (type === 'local') {
            storage.local.removeItem(key);
            storage.local.removeItem(key + config.ttlKey);
        }

        // Log
        if (type === 'local' || type === 'session') {
            engine.debug.info('Removed `' + key + '` from ' + type + ' storage');
        }
    };

    // Remove all items (!)
    api.removeAll = function(type) {
        var keys = Object.keys(type === 'local' ? storage.local : storage.session);

        if (!type || !keys.length) {
            return false;
        }

        $.each(keys, function(i) {
            api.remove(type, keys[i]);
        });
    };

    // Remove all expired items
    api.clean = function() {
        // Get all local keys
        var storage = api.getAll('local');
        var cleaned = [];

        if (storage && Object.size(storage)) {
            $.each(storage, function(key) {
                // Grab all TTL keys
                if (key.indexOf(config.ttlKey) === key.length - config.ttlKey.length && key.indexOf(config.ttlKey) > 0) {
                    // Reset key to original
                    key = key.replace(config.ttlKey, '');

                    // Get the value
                    var value = api.get('local', key);

                    // If it's no longer valid or empty, clean it
                    if (!valid(key) || (typeof value === 'object' && Object.size(value) === 0) || value.length === 0) {
                        api.remove('local', key);
                        cleaned.push(key);
                    }
                }
            });

            if (cleaned.length) {
                engine.debug.info('Cleaned up `' + cleaned.join(',') + '`');
            }
        }
    };

    // Check if an item exists and is valid (if local)
    api.exists = function(type, key) {
        if (type === 'session' && storage.session.getItem(key) !== null) {
            return true;
        }
        if (type === 'local' && storage.local.getItem(key) !== null) {
            // If it's a cookie, we know it exists...
            if (!supportsLocalStorage()) {
                return true;
            }
            // Check for ttl key if using storage
            if (key.indexOf(config.ttlKey) > 0 || valid(key)) {
                return true;
            }
            // If it's true persisted storage
            if (storage.local.getItem(key + config.ttlKey) === null) {
                return true;
            }
        }
        return false;
    };

    engine.register(api.init);
    return api;
})(engine.storage || {});

// ==========================================================================
// PostMessage handlers
// ==========================================================================

var engine = engine || {};

engine.messages = (function(api) {
    var config;
    const { is, redirect } = engine.utils;

    api.init = function(settings) {
        config = {
            domain: '*', // Post to anyone listening
        };

        $.extend(true, config, settings);

        setup();
    };

    function setup() {
        listeners();
    }

    function listeners() {
        // Bind handler to window
        $(window).on('message', messageHandler);
    }

    function messageHandler(event) {
        event = event.originalEvent;
        var message = event.data;

        // Try and parse as JSON
        if (is.string(message)) {
            try {
                message = JSON.parse(message);
            } catch (e) {
                // Do nothing
                message = null;
            }
        }

        // Ignore if not in our format
        if (!is.object(message) || !Object.keys(message).includes('key')) {
            return;
        }

        // Handle message
        switch (message.key) {
            case 'redirect':
                redirect(message.data.url, message.data.target);
                break;
        }

        // Debugging
        engine.debug.log(`Received from ${event.origin}:`, message);

        // Set source
        message.source = event.source;
        message.origin = event.origin;

        // Pub sub for other handlers
        engine.publish(engine.siren.window.message, [message]);
    }

    // Post to parent
    api.post = function(message, domain, target) {
        // Message required
        if (is.empty(message)) {
            // engine.debug.warn('Message is empty');
            return;
        }

        // Key is required
        if (!Object.keys(message).includes('key')) {
            // engine.debug.warn('Message is missing key');
            return;
        }

        var url = domain ? domain : config.domain;

        // Set the target
        if (is.jQuery(target)) {
            var frame = target.get(0);

            if (!is.htmlElement(frame)) {
                return;
            }

            target = frame.contentWindow;
        }

        // If no target, assume opener (window.open) or parent (iframe)
        if (!target) {
            target = window.opener ? window.opener : window.parent;
        }

        // Don't post to yourself
        /* if (target === window.self) {
        // engine.debug.warn('Message to self ignored');
            return;
        } */

        // Send the message
        target.postMessage(JSON.stringify(message), url);

        // Debugging
        engine.debug.log(`Sent to '${url}':`, message);
    };

    engine.register(api.init);

    return api;
})(engine.messages || {});

// expose engine to window when importing to storybook webpack
window.engine = engine;
